.marks{
  display: flex;
  /*align-items: center;*/
  /*justify-content: center;*/
  flex-direction: column;
  margin-left: 30px;
}
.mark_type {
  width: 100%;
  display: flex;
  flex-direction: row;
}
.mark {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 1px solid #080808;
  margin-left: 0;
  margin-right: -1px;
  margin-top: -1px;

}

.type_name {
  width: 16ch;
}
input {
  width: 80%;
  height: 80%;
  border: 0;
}
.possible-marks {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
}
.mark-wrapper{
  display: flex !important;
  flex-direction: column;
  padding: 0 10px 0 10px;

}
h3 {
  padding: 5px;
  margin: 5px;
}